#ReseauxButtonsDiv
{
    display: grid;
    justify-content: center;
    grid-column-gap: 8vw;
}
.PopUpBTN.Reseaux
{
    position: absolute;
    z-index: 1;
}

.PopUpBTNReseaux_Wave
{
    position: relative;
    z-index: 0;
    width: 80%;
}


#C5
{
    grid-area: a;
}

#BNEI
{
    grid-area: e;
}

#AssoM
{
    grid-area: b;
}

#AI
{
    grid-area: i;
}

@media only screen and (min-width: 768px) {
    #ReseauxButtonsDiv
    {
        margin-top: 2vh;
        height: 90%;
        grid-template-areas:
                "a i"
                "b e";
    }

    .ReseauxButtons .PopUpBTN
    {
        height: 38vh;
    }
}

@media only screen and (max-width: 767px) {
    #ReseauxButtonsDiv
    {
        grid-template:
                "a ." max-content
                ". i" max-content
                "b ." max-content
                ". e" max-content;
        height: 78vh;
        justify-content: center;
        margin-left: 5vw;
        margin-right: 5vw;
        grid-gap:0;
        margin-top: 2vh;
    }

    .ReseauxButtons:nth-child(even)
    {
        position: relative;
        left:0;
    }

    .ReseauxButtons .PopUpBTN
    {
        height: 23vh;
    }

    .PopUpBTNReseaux_Wave
    {
        width: 22vh;
    }
    .PopUpBTN{
        margin: auto;
    }
}
